import { Component } from "react";
import { gql } from "apollo-boost";
import { Mutation } from "react-apollo";

const DELETE_MOVIE_MUTATION = gql`
  mutation DELETE_MOVIE_MUTATION($id: ID!) {
    deleteMovie(where: { id: $id }) {
      id
    }
  }
`;

class DeleteMovie extends Component {
  update = deleteMovie => {
    console.log("movie deleted!");
    deleteMovie({ id: this.props.id });
  };
  render() {
    return (
      <Mutation
        mutation={DELETE_MOVIE_MUTATION}
        variables={this.props.id}
        update={this.update}
      >
        {(deleteMovie, payload) => (
          <button onClick={() => this.update(deleteMovie)}>x</button>
        )}
      </Mutation>
    );
  }
}

export default DeleteMovie;
